<script>
    // Listes de toutes les tapes et thmatiques disponible
    export let tapes;
    export let thmatiques;

    export let tapeFilterChange;
    export let thmatiqueFilterChange;

    // Object complexe : qui contient tapes et thmatiques slectionnes par l'utilisateur.rice
    export let filters;

