<script>
    import ResourceFilters from "./ResourceFilters.svelte";
    import ResourceList from "./ResourceList.svelte";
    import Squelette from "./Squelette.svelte";

    import findRelevantResourcesFromFilters from '../findRelevantResourcesFromFilters.js';

    export let allEtapes;
    export let allThmatiques;
    export let allResources;

    export let bookmarkedResourceIdSet;

    export let makeBookmarkResource;
    export let makeUnbookmarkResource;
    
    export let listeRessourceURL;

    let userFilters = {
        tapes: new Set(),
        thmatiques: new Set()
    };

    let tapeFilterChange = function toggletapeFilter(tape){
        console.log("tape dans filterChange: ", tape)
        if(userFilters.tapes.has(tape))
            userFilters.tapes.delete(tape)
        else
            userFilters.tapes.add(tape)
        
        userFilters = userFilters // to trigger svelte re-render
    };

    let thmatiqueFilterChange = function toggleThmatiquesFilter(thmatique){
        if(userFilters.thmatiques.has(thmatique))
            userFilters.thmatiques.delete(thmatique)
        else
            userFilters.thmatiques.add(thmatique)

        userFilters = userFilters // to trigger svelte re-render
    };

    let filters = {
        tapes: new Set(allEtapes),
        thmatiques: new Set(allThmatiques)
    }

    let relevantResources = [];
    $: {
        // building filters by substracting all tapes/thmatiques from userFilters
        filters = {
            tapes: new Set(allEtapes),
            thmatiques: new Set(allThmatiques)
        };

        for(const e of userFilters.tapes){
            filters.tapes.delete(e)
        }

        for(const t of userFilters.thmatiques){
            filters.thmatiques.delete(t)
        }

        relevantResources = findRelevantResourcesFromFilters(allResources, filters)
    }
    
</script>

<Squelette {listeRessourceURL}>
    <svelte:fragment slot="colonne-du-centre">
        <h3>Toutes les Ressources</h3>

        <ResourceFilters
            tapes={allEtapes}
            thmatiques={allThmatiques}
            {filters}
            {tapeFilterChange}
            {thmatiqueFilterChange}
        />
        <h4>{relevantResources.length} Ressources trouves</h4>

        <ResourceList
            resources={relevantResources}
            {makeBookmarkResource}
            {makeUnbookmarkResource}
            {bookmarkedResourceIdSet}
        />
    </svelte:fragment>
    
    
    
</Squelette>

